.root {
  height: 100%;
  gap: 10px;
  display: grid;
  grid-template-areas:
    "export export"
    "edits player";
  grid-template-columns: minmax(200px, 400px) 400px;

  justify-content: center;
  /* align-items: center; */
}

.export {
  grid-area: export;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 0 10px;
}

.edits {
  grid-area: edits;
  overflow-y: auto;
}

.player {
  grid-area: player;
}
